.pages{
    width: 100%;
    
}
.pagediv{
    color: black;
    justify-content: space-around;
    font-family: 'Nunito', sans-serif;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 95%;
}   
.title{
    margin-top: 10vh;
}
.buttons{
    margin-top: 45vh;
}

.button1{
    background-color: rgba(47, 47, 47, 0.9);
    padding: 11px 80px;
    border: none;
    border-radius: 5px;
    color: white;
    font-size: 15px;
    margin-right: 12px;
    letter-spacing: 1px;
}

.button2{
    background-color: rgba(233, 233, 233, 0.9);
    padding: 11px 85px;
    border: none;
    border-radius: 5px;
    color: rgb(47, 47, 47);
    font-size: 15px;
    margin-left: 12px;
    letter-spacing: 1px;
}

button:hover{
    cursor: pointer;
}

.title > h1{
    font-size: 40px;
    margin: 5px 0;
    letter-spacing: 1px;
}

.title > p{
    font-size: 18px;
    margin: 5px 0;
    letter-spacing: 1px;
}
.expand{
    z-index: 100;
    height: 20px;
    width: 20px;
}