nav{
    height: 56px;
    width: 100vw;
    display: flex;
    position: fixed;
    z-index: 10;
    transition: 0.2s;
    top: 0;
    justify-content: space-between;
    font-family: 'Nunito', sans-serif;
    font-size: 14px;
    letter-spacing: 1px;
}


/*Styling Links*/
.nav-links{
    display: flex;
    list-style: none; 
    padding: 0 0.7vw;
    justify-content: space-evenly;
    align-items: center;
}
.nav-links li a{
    text-decoration: none;
    margin: 0 0.1vw;
    color: black;
    padding: 5px 15px;
    border-radius: 5px;
}
.nav-links li a:hover{
    background-color: rgba(194, 194, 194, 0.4);
}

#tesla li a:hover{
    background-color: transparent;
}

.nav-links li {
    position: relative;
}


/*Styling Hamburger Icon*/
.hamburger div{
    width: 30px;
    height:3px;
    background: #f2f5f7;
    margin: 5px;
    transition: all 0.3s ease;
}
.hamburger{
    display: none;
}

/*Stying for small screens*/
@media screen and (max-width: 800px){
    nav{
        position: fixed;
        z-index: 3;
    }
    .hamburger{
        display:block;
        position: absolute;
        cursor: pointer;
        right: 5%;
        top: 50%;
        transform: translate(-5%, -50%);
        z-index: 2;
        transition: all 0.7s ease;
    }
    .nav-links{
        position: fixed;
        background: #151515;
        height: 100vh;
        width: 100%;
        flex-direction: column;
        clip-path: circle(50px at 90% -20%);
        -webkit-clip-path: circle(50px at 90% -10%);
        transition: all 0.8s ease-out;
        pointer-events: none;
    }
    .nav-links.open{
        clip-path: circle(1000px at 90% -10%);
        -webkit-clip-path: circle(1000px at 90% -10%);
        pointer-events: all;
    }
    .nav-links li{
        opacity: 0;
    }
    .nav-links li:nth-child(1){
        transition: all 0.5s ease 0.2s;
    }
    .nav-links li:nth-child(2){
        transition: all 0.5s ease 0.4s;
    }
    .nav-links li:nth-child(3){
        transition: all 0.5s ease 0.6s;
    }
    li.fade{
        opacity: 1;
    }
}
/*Animating Hamburger Icon on Click*/
.toggle .line1{
    transform: rotate(-45deg) translate(-5px,6px);
}
.toggle .line2{
    transition: all 0.7s ease;
    width:0;
}
.toggle .line3{
    transform: rotate(45deg) translate(-5px,-6px);
}
.tesla{
    text-align: left;
    
}
.teslalogo{
    height: 22px;
    margin-left: 25px;
    margin-right: 6vw;
}
#menu {
    letter-spacing: 2px;
}